<template>
    <!---->
    <el-row class="member-manage">
        <p style="color:#999">*<strong>: </strong></p>
        <div style="margin-bottom: 20px;font-size: 20px">
            <div>
                <div style="display: inline"> </div>
                <div v-if="!reportFrom" style="display: inline"></div>
                <div v-if="reportFrom" style="display: inline">{{reportFrom}}</div>

                &nbsp;&nbsp;
                <i class="el-icon-edit" style="cursor:pointer;display: inline" @click="editFormVisible=true"></i>
                                &nbsp;&nbsp;
                <i v-if="reportFrom" class="el-icon-delete" style="cursor:pointer;display: inline" @click="DelEmail()"></i>
            </div>
        </div>
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false" style="width: 60%; left: 20%">
		    <el-form :model="editForm" label-width="100px"  :rules="editFormRules" ref="editForm">
                <el-form-item label=":" prop="reportFrom">
                    <el-input v-model="editForm.reportFrom" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="mailUser">
                    <el-input v-model="editForm.mailUser" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="mailPass">
                    <el-input v-model="editForm.mailPass" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="mailSmtp">
                    <el-input v-model="editForm.mailSmtp" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
		</el-dialog>
        <el-col :span="24">
            <el-table :data="memberData" highlight-current-row v-loading="listLoading" style="width: 100%;">
                <el-table-column prop="username" label="" min-width="30%" sortable>
                </el-table-column>
                <el-table-column prop="permissionType" label="" min-width="30%" sortable>
                </el-table-column>
                <el-table-column prop="userPhone" label="" min-width="20%" sortable>
                </el-table-column>
                <el-table-column prop="userEmail" label="" min-width="20%" sortable>
                </el-table-column>
            </el-table>
            <!---->
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
            </el-pagination>
        </el-col>
    </el-row>
</template>

<script>
    import { test } from '../../api/api'
    import $ from 'jquery'
    export default {
        data() {
            return {
                memberData: [],
                total: 0,
                page: 1,
                listLoading: false,
                reportFrom: "",
                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    reportFrom: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }
                    ],
                    mailUser: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }
                    ],
                    mailPass: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }
                    ],
                    mailSmtp: [
                        { required: false, message: '', trigger: 'blur' },
                        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }
                    ]
                },
                //
                editForm: {
                },
            }
        },
        methods: {
            handleCurrentChange(val) {
                this.page = val;
                this.getProjectMember()
            },
            // HOST
            getProjectMember() {
                this.listLoading = true;
                let self = this;
                $.ajax({
                    type: "get",
                    url: test+"/api/member/project_member",
                    async: true,
                    data: { project_id: this.$route.params.project_id, page: self.page},
                    headers: {
                        Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                    },
                    timeout: 5000,
                    success: function(data) {
                        self.listLoading = false;
                        if (data.code === '999999') {
                            self.total = data.data.total;
                            self.memberData = data.data.data
                        }
                        else {
                            self.$message.error({
                                message: data.msg,
                                center: true,
                            })
                        }
                    },
                })
            },
            getEmailConfig(){
                let self = this;
                $.ajax({
                    type: "get",
                    url: test+"/api/member/get_email",
                    async: true,
                    data: { project_id: this.$route.params.project_id},
                    headers: {
                        Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                    },
                    timeout: 5000,
                    success: function(data) {
                        self.listLoading = false;
                        if (data.code === '999999') {
                            if (data.data.length) {
                                self.reportFrom = data.data[0].reportFrom;
                                self.editForm = data.data[0]
                            } else {
                                self.reportFrom = "";
                                self.editForm = {}
                            }
                        }
                        else {
                            self.$message.error({
                                message: data.msg,
                                center: true,
                            })
                        }
                    },
                })
            },
            DelEmail(){
                let self = this;
                $.ajax({
                    type: "post",
                    url: test+"/api/member/del_email",
                    async: true,
                    data: { project_id: this.$route.params.project_id},
                    headers: {
                        Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                    },
                    timeout: 5000,
                    success: function(data) {
                        self.listLoading = false;
                        if (data.code === '999999') {
                            self.$message.success({
                                message: "",
                                center: true,
                            })
                            self.getEmailConfig()
                        }
                        else {
                            self.$message.error({
                                message: data.msg,
                                center: true,
                            })
                        }
                    },
                })
            },
            editSubmit: function () {
                let self = this;
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            self.editLoading = true;
                            //NProgress.start();
                            console.log(this.editForm)
                            $.ajax({
                                type: "post",
                                url: test+"/api/member/email_config",
                                async: true,
                                data: {
                                    project_id: this.$route.params.project_id,
                                    from: this.editForm.reportFrom,
                                    user: this.editForm.mailUser,
                                    mailPass: this.editForm.mailPass,
                                    mailSmtp: this.editForm.mailSmtp,
                                },
                                headers: {
                                    Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                                },
                                timeout: 5000,
                                success: function(data) {
                                    self.editLoading = false;
                                    if (data.code === '999999') {
                                        self.$message({
                                            message: '',
                                            center: true,
                                            type: 'success'
                                        });
                                        self.$refs['editForm'].resetFields();
                                        self.editFormVisible = false;
                                        self.getEmailConfig()
                                    } else if (data.code === '999997'){
                                        self.$message.error({
                                            message: data.msg,
                                            center: true,
                                        })
                                    } else {
                                        self.$message.error({
                                            message: data.msg,
                                            center: true,
                                        })
                                    }
                                },
                            })
                        });
                    }
                });
            },
        },
        mounted() {
            this.getProjectMember();
            this.getEmailConfig();

        }
    }
</script>

<style lang="scss" scoped>
    .member-manage {
        margin: 35px;
    }
</style>


